.app-wrapper {
    margin: 10px;
    min-height: 95vh;
    background-color: silver;
    box-shadow: 0 0 10px 2px rgba(0,0,0,0.1);
    border: outset;
}
.app-wrapper-block{
    display: flex;
    flex-direction: row;
}

.app-wrapper-content {
    display: flex;
    justify-content: center;
}