.loginBlock {
    width: 400px;
    border: 2px outset;
    padding: 5px;
    margin-top: 150px;
    margin-left: 450px;
}
.loginTitle{
    padding: 2px;
    background-color: darkblue;
    border: 2px inset;
    color: white;
    font-size: 20px;
}
.loginContainer{
    margin-top: 10px;
}
.inputsContainer input{
    border: 2px inset;
    width: 386px;
    padding: 5px;
    outline: none;
    margin-bottom: 7px;
    background-color: white;
}
.rememberMe{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}
.rememberMe button{
    width: 100px;
    height: 30px;
    border: 2px outset;
}
.rememberMe button:active{
    border: 2px inset;
}
.formSummaryError{
    width: 390px;
    position: absolute;
    margin-top: 25px;
    margin-left: -7px;
    background-color: red;
    padding: 10px;
    border: 2px red outset;
    font-weight: bold;
}
.captchaUrl{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 10px;
}
.captchaUrl input{
    margin-top: 7px;
    border: 2px inset;
}