.header {
    margin: 5px;
    width: 1326px;
    height: 40px;
    background-color: darkblue;
    border: 2px inset;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.loginBlock button {
    border: 2px outset;
    background-color: silver;
    padding: 5px;
    font-size: 17px;
    margin-right: 3px;
}
.loginBlock button:active{
    border: 2px inset;
}